import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';

import { IonicModule } from '@ionic/angular';

import { SuportePageRoutingModule } from './suporte-routing.module';

import { SuportePage } from './suporte.page';
import { AccordionModule } from 'primeng/accordion';
import { MessageService } from 'primeng/api';
import { ToastModule } from 'primeng/toast';
import { InputTextareaModule } from 'primeng/inputtextarea';
import { ButtonModule } from 'primeng/button';
import { TabViewModule } from 'primeng/tabview';
import { PanelModule } from 'primeng/panel';
import { DropdownModule } from 'primeng/dropdown';
import { MessageModule } from 'primeng/message';
import { LoadSpinnerModule } from '../components/utils/load-spinner/load-spinner.module';
import { FooterModule } from '../components/footer/footer.module';

@NgModule({
  imports: [
    CommonModule,
    FormsModule,
    ReactiveFormsModule,
    IonicModule,
    SuportePageRoutingModule,
    AccordionModule,
    ToastModule,
    InputTextareaModule,
    ButtonModule,
    TabViewModule,
    PanelModule,
    DropdownModule,
    MessageModule,
    LoadSpinnerModule,
    FooterModule
  ],
  providers: [MessageService],
  declarations: [SuportePage]
})
export class SuportePageModule { }
